import godetail from '../home/godetail.vue'
export default {
	data() {
		return {
			params:{
				limit:20,
				order:'new',
				skip:0,
				first:1
			},
			id:-1,
			wallpaperst:[],
			album:{},
			haspc:true,
		}
	},
	onLoad(options) {
		console.log(options);
		this.id = options.id
		this.reques()
	},
	onReachBottom() {
		//自带的滚动事件函数，监听整个事件的滚动。
		if(this.haspc ===false)
		{
			uni.showToast({
				title:'没有图片了',
				icon:'none'
			})
			return;
		}
		this.params.first = 0
		this.params.skip = this.params.limit+this.params.skip
		//在这里修改请求参数
		console.log('哈哈哈')
		this.request({
		url:`http://157.122.54.189:9088/image/v1/wallpaper/album/${this.id}/wallpaper`,
		data:this.params	
		}).then((res)=>{
		console.log(res);
		if(res.res.wallpaper.length===0){
			this.haspc = false
		}
		this.wallpaperst = [...this.wallpaperst,...res.res.wallpaper]
		})
		
	},
	components:{
		godetail
	},
	methods: {
		reques() {
			this.request({
			url:`http://157.122.54.189:9088/image/v1/wallpaper/album/${this.id}/wallpaper`,
			data:this.params	
			}).then((res)=>{
			console.log(res);
			this.album = res.res.album
			this.wallpaperst = res.res.wallpaper
			})
		}
	},
}
